define('borrowers/controllers/articles/new', ['exports', 'ember'], function (exports, Ember) {

  'use strict';

  exports['default'] = Ember['default'].Controller.extend({
    actions: {
      save: function save() {
        if (this.get('model.description')) {
          return true;
        } else {
          this.set('errorMessage', '没有description这个属性');
        };
      }
    }
  });

});